.btn-up {
  width: 50px;
  height: 50px;
  color: #000;
  opacity: 0;
  pointer-events: none;
  z-index: 9000;
  cursor: pointer;
  background: rgba(255, 255, 255, .75);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-size: 32px;
  text-decoration: none;
  transition: all .4s;
  display: flex;
  position: fixed;
  bottom: 16px;
  right: 45px;
}

.btn-up:before {
  width: 100%;
  height: 100%;
  content: "â†‘";
  justify-content: center;
  align-items: center;
  font-size: 30px;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.btn-up.active {
  pointer-events: auto;
  opacity: 1;
  bottom: 32px;
}

.btn-up:hover {
  color: #fff;
  background-color: #020202;
}

